export default {
	data() {
		return {
			signature:"这个人很懒，什么都没留下"
		};
	},
	methods:{
		getdata(){
			uni.getStorage({
				key: 'signature',
				// data: res.tempFilePaths,
				success:res=> {
					this.signature=res.data;
				},fail: () => {
					this.signature="一路相伴，感谢有你"
				}
			});	
		},
		submit(){
				var data=this.signature;
				console.log(data,"6666666s")
				uni.showModal({
					content:"是否确认修改?",
					// mask:true,
					showCancel:true,
					success:res=>{
						console.log(res);
						if (res.confirm) {
							console.log(this.signature,"ggggggggggggggggggggggg"),
							uni.setStorage({
								key: 'signature',
								data,
								success: function () {
									uni.navigateTo({
										url:"/pages/user/setting/setting"
										// animationDuration:1000
									})
								}
							});								
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
					
				})
		}
		
	},
	onReady(){
		this.getdata()
	},
	onShow(){
			this.getdata()
	},
	onLoad() {
		this.getdata()
	}
}
